.advantages {
  padding: 100px 0px;
  background: #f9f9f9;
  @media (max-width: $breakpoint576) {
    padding: 50px 0;
  }
  &__title {
    @media (max-width: $breakpoint576) {
      text-align: center;
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -20px;
    @media (max-width: $breakpoint768) {
      margin: 0;
    }
  }

  &__item {
    width: calc((100% - 3 * 40px) / 3);
    margin: 0 20px;
    @media (max-width: 768px) {
      width: calc(100%);
      margin: 0;
      &:not(:last-child) {
        margin-bottom: 30px;
      }
    }
    @media (max-width: $breakpoint576) {
      text-align: center;
    }
  }

  &__subtitle {
    font-weight: 600;
    font-size: 24px;
    line-height: 160%;
    color: $dark-color;
  }

  &__text {
    color: rgba($dark-color, 0.7);
  }
}

.tooltip {
  border-radius: 100%;
  width: 24px;
  height: 24px;
  background: rgba(20, 16, 36, 0.08);
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin-left: 10px;
  &:hover + .tooltip__text {
    opacity: 1;
  }

  svg {
    width: 3px;
    height: 13px;
  }

  &__text {
    display: inline-block;
    font-size: 16px;
    line-height: 160%;
    text-align: center;
    color: $dark-color;
    background: #ffffff;
    box-shadow: 0px 2px 8px rgba(20, 16, 36, 0.1);
    padding: 15px 20px;
    border-radius: 5px;
    opacity: 0;
    transition: opacity 0.5s;
    @media (max-width: $breakpoint576) {
      padding: 15px 20px;
      font-size: 14px;
    }
  }
}
