.trainers {
  &__img {
    border-radius: 20px;
  }

  &__list {
    margin-bottom: 77px;
    max-width: 1190px;
  }
  &__name {
    margin-top: 20px;
    font-weight: 600;
    font-size: 24px;
    line-height: 160%;

    color: #141024;
  }

  &__text {
    margin-top: 10px;
    color: rgba(20, 16, 36, 0.7);
  }

  &__btn {
    margin-top: 20px;
    font-size: 18px;
    line-height: 160%;

    color: $accent-color;
  }
  &__prev {
    svg {
      width: 40px;
      height: 40px;
      position: absolute;
      right: 60px;
      bottom: 0px;
    }
    @media (max-width: $breakpoint576) {
      display: none;
    }
  }

  &__next {
    svg {
      width: 40px;
      height: 40px;
      transform: rotate(180deg);
      position: absolute;
      right: 10px;
      bottom: 0px;
    }
    @media (max-width: $breakpoint576) {
      display: none;
    }
  }
  &__scroll {
    width: 360px !important;
    background: rgba(20, 16, 36, 0.08);
    border-radius: 40px;
    .swiper-scrollbar-drag {
      background: #141024;
      border-radius: 40px;
    }
  }
}
.swiper-button-disabled {
  svg {
    fill: #230b34;
  }
}
