.about {
  &__title {
    max-width: 891px;
    font-weight: 400;
    font-size: 30px;
    color: #141024;
    line-height: 160%;
    margin-bottom: 60px;
    @media (max-width: $breakpoint576) {
      font-size: 20px;
      margin-bottom: 40px;
    }
    span {
      font-weight: 600;
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -20px;
    @media (max-width: $breakpoint576) {
      margin: 0;
      flex-direction: column;
      align-items: center;
    }
  }

  &__item {
    width: calc((100% - 4 * 40px) / 4);
    margin: 0 20px;
    @media (max-width: $breakpoint992) {
      width: calc((100% - 2 * 40px) / 2);
    }
    @media (max-width: $breakpoint576) {
      width: 100%;
      margin: 0;
      text-align: center;

      &:not(:last-child) {
        margin-bottom: 30px;
      }
    }
  }

  &__num {
    font-weight: 600;
    font-size: 70px;
    line-height: 160%;
    white-space: nowrap;
    color: $accent-color;
    @media (max-width: $breakpoint576) {
      font-size: 60px;
    }
  }

  &__text {
    color: rgba($dark-color, 0.7);
  }
}
