.hero {
  background-color: $dark-color;
  color: #ffffff;
  position: relative;
  padding-top: 344px;
  padding-bottom: 411px;

  @media (max-width: $breakpoint992) {
    padding-top: 280px;
    padding-bottom: 350px;
  }

  @media (max-width: $breakpoint768) {
    padding-top: 240px;
    padding-bottom: 350px;
  }

  @media (max-width: $breakpoint576) {
    padding-top: 101px;
    padding-bottom: 40px;
  }

  &__container {
  }

  &__content {
    max-width: 516px;
    @media (max-width: $breakpoint576) {
      text-align: center;
      position: relative;
    }
  }

  &__title {
    font-weight: 600;
    font-size: 70px;
    margin: 0;
    margin-bottom: 35px;
    line-height: 160%;
    @media (max-width: 992px) {
      font-size: 60px;
    }
    @media (max-width: 576px) {
      font-size: 30px;
      color: #ffffff;
      margin-bottom: 10px;
    }
  }

  &__text {
    font-size: 20px;
    line-height: 160%;
    margin-bottom: 52px;
    @media (max-width: $breakpoint576) {
      margin-bottom: 280px;
      font-size: 16px;
    }
  }

  &__btn {
    display: inline-block;
    position: relative;
    z-index: 2;
    @media (max-width: $breakpoint576) {
      font-size: 16px;
      line-height: 100%;
      width: 100%;
    }
  }
  &__img {
    margin-top: 162px;
    position: absolute;
    bottom: 0;
    right: 11.09375%;
    @media (max-width: 1800px) {
      width: 600px;
    }
    @media (max-width: 1500px) {
      width: 500px;
    }
    @media (max-width: 1199.98px) {
      width: 400px;
    }
    @media (max-width: 992px) {
      width: 350px;
    }
    @media (max-width: 768px) {
      width: 300px;
    }
    @media (max-width: $breakpoint576) {
      bottom: 0;
      width: 273px;
      left: 50%;
      transform: translateX(-50%);
    }
  }
}
