.footer__row {
  position: relative;
  display: flex;
  justify-content: space-between;
}
@media (max-width: 575.98px) {
  .footer__row {
    flex-direction: column;
    text-align: center;
  }
}
@media (max-width: 575.98px) {
  .footer__logo {
    margin-bottom: 86px;
    display: inline-flex;
  }
}
.footer__copy {
  font-size: 16px;
  line-height: 160%;
  color: rgba(255, 255, 255, 0.7);
  margin-top: 5px;
}
@media (max-width: 575.98px) {
  .footer__right {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
}
.footer__phone {
  white-space: nowrap;
}
@media (max-width: 575.98px) {
  .footer__phone {
    font-weight: 600;
    font-size: 16px;
    line-height: 160%;
  }
}

.graph-modal__close {
  font-size: 18px;
  line-height: 160%;
  color: rgba(20, 16, 36, 0.7);
  background-image: none;
  top: 40px;
  right: 40px;
  width: auto;
  height: auto;
}
@media (max-width: 767.98px) {
  .graph-modal__close {
    top: 20px;
    right: 20px;
  }
}
@media (max-width: 575.98px) {
  .graph-modal__close {
    top: 10px;
    right: 20px;
    font-size: 0;
  }
  .graph-modal__close:before, .graph-modal__close:after {
    position: absolute;
    content: " ";
    height: 18px;
    top: 0;
    width: 2px;
    background-color: #333;
  }
  .graph-modal__close:before {
    transform: rotate(45deg);
  }
  .graph-modal__close:after {
    transform: rotate(-45deg);
  }
}

.popup {
  padding: 40px;
}
@media (max-width: 575.98px) {
  .popup {
    padding: 33px 20px;
  }
}
.popup__top {
  display: flex;
  margin-bottom: 40px;
}
@media (max-width: 575.98px) {
  .popup__top {
    margin-bottom: 25px;
  }
}
@media (max-width: 575.98px) {
  .popup__img {
    width: 130px;
  }
}
.popup__info {
  margin-left: 40px;
}
@media (max-width: 767.98px) {
  .popup__info {
    margin-left: 20px;
  }
}
@media (max-width: 575.98px) {
  .popup__info {
    margin-left: 10px;
  }
}
.popup__name {
  font-weight: 600;
  font-size: 24px;
  line-height: 160%;
  margin-bottom: 10px;
  color: #141024;
}
@media (max-width: 575.98px) {
  .popup__name {
    margin-bottom: 3px;
    font-size: 16px;
  }
}
.popup__post {
  font-size: 18px;
  line-height: 160%;
  margin-bottom: 10px;
  color: rgba(20, 16, 36, 0.7);
}
@media (max-width: 575.98px) {
  .popup__post {
    font-size: 14px;
  }
}
.popup__social {
  display: flex;
  align-items: center;
}
.popup__item:not(:last-child) {
  margin-right: 10px;
}
.popup__link:hover svg {
  fill: blue;
}
.popup__descr {
  font-size: 20px;
  line-height: 160%;
  margin-bottom: 40px;
  color: #141024;
}
.popup__content {
  font-size: 20px;
  line-height: 160%;
  color: #141024;
}
.popup__body {
  max-height: 378px;
}
.popup__subtitle {
  font-weight: 600;
  font-size: 24px;
  line-height: 160%;
  color: #141024;
  margin-bottom: 20px;
}

.tabs__nav {
  display: flex;
  justify-content: space-between;
  max-width: 678px;
  margin-bottom: 20px;
}
@media (max-width: 575.98px) {
  .tabs__nav {
    flex-direction: column;
    align-items: center;
  }
}
.tabs__nav-item {
  margin-right: 20px;
}
@media (max-width: 575.98px) {
  .tabs__nav-item {
    margin-right: 0;
    margin-bottom: 20px;
  }
}
.tabs__nav-btn--active {
  color: #d52027;
}
.tabs__nav-btn:focus {
  color: #d52027;
}
.tabs__content {
  padding-top: 20px;
  border-top: 1px solid rgba(20, 16, 36, 0.11);
  max-height: 378px;
}
.tabs__panel {
  display: none;
}
.tabs__panel--active {
  display: block;
}

.simplebar-track.simplebar-vertical {
  background: rgba(20, 16, 36, 0.08);
  border-radius: 40px;
  width: 5px;
  margin-top: 20px;
}

.simplebar-scrollbar:before {
  left: 0;
  right: 0;
  width: 5px;
  opacity: 1;
  background: #141024;
  border-radius: 40px;
}