.footer {
  padding: 40px 0;
  background: $dark-color;

  &__row {
    position: relative;
    display: flex;
    justify-content: space-between;
    @media (max-width: $breakpoint576) {
      flex-direction: column;
      text-align: center;
    }
  }

  &__left {
  }
  &__logo {
    @media (max-width: $breakpoint576) {
      margin-bottom: 86px;
      display: inline-flex;
    }
  }
  &__copy {
    font-size: 16px;
    line-height: 160%;
    color: rgba(255, 255, 255, 0.7);
    margin-top: 5px;
  }
  &__right {
    @media (max-width: $breakpoint576) {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
    }
  }
  &__phone {
    white-space: nowrap;
    @media (max-width: $breakpoint576) {
      font-weight: 600;
      font-size: 16px;
      line-height: 160%;
    }
  }
}
