//BEM block
.phone {
  font-weight: 600;
  font-size: 30px;
  line-height: 160%;
  color: #ffffff;
}

.section {
  margin-bottom: 120px;
  @media (max-width: $breakpoint576) {
    margin-bottom: 60px;
  }
}

.title {
  font-weight: 600;
  font-size: 50px;
  line-height: 160%;
  color: #141024;
  margin-bottom: 40px;
  @media (max-width: $breakpoint768) {
    font-size: 40px;
  }
  @media (max-width: $breakpoint576) {
    font-size: 30px;
    text-align: center;
  }
}

.btn {
  text-align: center;
  color: #ffffff;
  background: $accent-color;
  border-radius: 5px;
  padding: 25px 54px;
}
