.header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  margin: 38px 0;
  z-index: 30;
  @media (max-width: $breakpoint768) {
    margin: 18px 0;
  }
  @media (max-width: $breakpoint576) {
    margin: 10px 0;
  }
  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &--fixed {
    position: fixed;
  }
  &__phone {
    transition: all 0.3s;
    &:hover {
      color: $accent-color;
    }
    @media (max-width: 992px) {
      margin-left: auto;
      margin-right: 40px;
    }
    @media (max-width: $breakpoint768) {
      background: url("../img/header-tel.svg") center center/cover;
      width: 44px;
      height: 44px;
      font-size: 0;
      margin-left: auto;
      margin-right: 20px;
    }
    @media (max-width: $breakpoint576) {
      margin-right: 10px;
    }
  }
}

.logo {
  &__img {
    @media (max-width: $breakpoint768) {
      width: 180px;
    }
    @media (max-width: $breakpoint576) {
      width: 136px;
      height: 29px;
    }
  }
}

.nav {
  @media (max-width: 992px) {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    overflow-y: auto;
    z-index: 500;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    background: #141024;
  }
  &__list {
    display: flex;
    align-items: center;
    @media (max-width: $breakpoint992) {
      flex-direction: column;
      align-items: flex-start;
    }
  }
  &--active {
    display: flex;
  }

  &__item {
    margin-right: 40px;
    @media (max-width: $breakpoint992) {
      margin-top: 20px;
      margin-right: 0;
    }
  }

  &__link {
    color: #ffffff;
    font-size: 20px;
    transition: all 0.3s;
    &:hover {
      color: $accent-color;
    }
    @media (max-width: $breakpoint992) {
      font-weight: 600;
      font-size: 30px;
      line-height: 160%;
    }
  }
}

.burger {
  display: none;
  align-items: center;
  justify-content: center;
  width: 44px;

  height: 44px;
  background: url("../img/header-bg.png");

  background-position: -4px 0;

  border-radius: 10px;
  @media (max-width: 992px) {
    display: flex;
  }

  &--active {
    position: fixed;
    top: 10px;
    right: 10px;
    z-index: 501;
    .burger__line {
      background: 0 0;
      width: 17px;
    }

    .burger__line::before {
      top: 0;
      width: 17px;
      transform: rotate(45deg);
    }

    .burger__line::after {
      top: 0;
      width: 17px;
      transform: rotate(-45deg);
    }
  }

  &__line {
    display: block;
    width: 26px;
    height: 2px;
    background-color: white;
    transition: all 0.3s ease-in-out;
    position: relative;
    border-radius: 4px;

    &::before,
    &::after {
      content: "";
      left: 0;
      position: absolute;
      width: 26px;
      height: 2px;
      background-color: white;
      transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
      border-radius: 4px;
    }

    &::before {
      top: -5px;
    }

    &::after {
      bottom: -5px;
      width: 50%;
    }
  }
}
