.order {
  background: #d52027 url(../img/order-bg.svg) 100% / cover no-repeat;
  padding: 87px 0;

  @media (max-width: $breakpoint768) {
    padding: 40px 0;
  }
  &__list {
    display: flex;
    flex-wrap: wrap;

    padding: 0 59px;
    @media (max-width: $breakpoint1200) {
      padding: 0;
    }
  }

  &__left {
    width: calc(50%);
    padding-top: 19px;
    @media (max-width: $breakpoint992) {
      margin-bottom: 40px;
    }
  }

  &__title {
    font-weight: 600;
    font-size: 36px;
    line-height: 130%;
    margin-bottom: 20px;
  }

  &__text {
    font-weight: 400;
    font-size: 20px;
    line-height: 130%;
  }

  &__right {
    width: calc(50%);
  }
  &__left,
  &__right {
    @media (max-width: $breakpoint992) {
      width: 100%;
    }
  }

  &__form {
    max-width: 500px;
    display: flex;
    flex-direction: column;
    @media (max-width: $breakpoint992) {
      max-width: 100%;
    }
  }
  &__label {
    display: flex;
    flex-direction: column-reverse;
    margin-bottom: 10px;
  }
  &__input {
    width: 100%;
    height: 64px;
    background: #f6f6f6;
    border-radius: 5px;
    font-size: 18px;
    line-height: 26px;
    padding: 19px 20px;
    color: $dark-color;
    &::placeholder {
      color: rgba(40, 41, 68, 0.7);
    }
  }

  &__btn {
    background: $dark-color;
    padding: 22px 54px;
  }
  .just-validate-error-label {
    display: inline-block !important;
    color: $dark-color !important;
    margin-bottom: 3px;
  }
}
