.price {
  &__list {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -20px;
    @media (max-width: $breakpoint576) {
      margin: 0;
    }
  }

  &__item {
    width: calc((100% - 3 * 40px) / 3);
    border-radius: 20px;
    color: #141024;
    border: 1px solid rgba(20, 16, 36, 0.2);
    margin: 0 20px;
    padding: 40px;
    text-align: center;
    display: flex;
    flex-direction: column;
    transition: all 0.2s ease-out;
    &:hover {
      background: #141024;
      cursor: pointer;
      color: #ffffff;
    }

    @media (max-width: $breakpoint992) {
      width: calc(100% - 40px);
      margin-bottom: 40px;
    }

    @media (max-width: $breakpoint576) {
      width: 100%;
      padding: 40px 20px;
      margin: 0;
    }
  }

  &__subtitle {
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 600;
    font-size: 30px;
    line-height: 160%;
    padding-bottom: 83px;
    &::before {
      content: "";
      background: currentColor;
      width: 19px;
      margin-right: 10px;
      height: 4px;
    }
    &::after {
      content: "";
      background: currentColor;
      width: 19px;
      margin-left: 10px;
      height: 4px;
    }
  }

  &__price {
    font-weight: 600;
    font-size: 50px;
    line-height: 160%;
    padding-bottom: 20px;
  }

  &__text {
    // padding-bottom: 52px;
    margin-bottom: auto;
  }

  &__btn {
    margin-top: 61px;
    font-size: 18px;
    line-height: 160%;
    color: #ffffff;
    @media (max-width: $breakpoint576) {
      font-size: 16px;
    }
  }
}
