.graph-modal__close {
  font-size: 18px;
  line-height: 160%;
  color: rgba(20, 16, 36, 0.7);
  background-image: none;
  top: 40px;
  right: 40px;
  width: auto;
  height: auto;

  @media (max-width: $breakpoint768) {
    top: 20px;
    right: 20px;
  }

  @media (max-width: $breakpoint576) {
    top: 10px;
    right: 20px;
    font-size: 0;

    &:before,
    &:after {
      position: absolute;
      content: " ";
      height: 18px;
      top: 0;
      width: 2px;
      background-color: #333;
    }
    &:before {
      transform: rotate(45deg);
    }

    &:after {
      transform: rotate(-45deg);
    }
  }
}
.popup {
  padding: 40px;

  @media (max-width: $breakpoint576) {
    padding: 33px 20px;
  }

  &__top {
    display: flex;
    margin-bottom: 40px;
    @media (max-width: $breakpoint576) {
      margin-bottom: 25px;
    }
  }

  &__img {
    @media (max-width: $breakpoint576) {
      width: 130px;
    }
  }

  &__info {
    margin-left: 40px;
    @media (max-width: $breakpoint768) {
      margin-left: 20px;
    }
    @media (max-width: $breakpoint576) {
      margin-left: 10px;
    }
  }

  &__name {
    font-weight: 600;
    font-size: 24px;
    line-height: 160%;
    margin-bottom: 10px;
    color: #141024;
    @media (max-width: $breakpoint576) {
      margin-bottom: 3px;
      font-size: 16px;
    }
  }

  &__post {
    font-size: 18px;
    line-height: 160%;
    margin-bottom: 10px;
    color: rgba(20, 16, 36, 0.7);
    @media (max-width: $breakpoint576) {
      font-size: 14px;
    }
  }

  &__social {
    display: flex;
    align-items: center;
  }

  &__item {
    &:not(:last-child) {
      margin-right: 10px;
    }
  }

  &__link {
    &:hover {
      svg {
        fill: blue;
      }
    }
  }

  &__bottom {
  }

  &__descr {
    font-size: 20px;
    line-height: 160%;
    margin-bottom: 40px;

    color: #141024;
  }

  &__text {
  }

  &__content {
    font-size: 20px;
    line-height: 160%;
    color: #141024;
  }

  &__body {
    max-height: 378px;
  }

  &__subtitle {
    font-weight: 600;
    font-size: 24px;
    line-height: 160%;
    color: #141024;
    margin-bottom: 20px;
  }
}
.js-modal-close {
}

.tabs {
  &__nav {
    display: flex;
    justify-content: space-between;
    max-width: 678px;
    margin-bottom: 20px;

    @media (max-width: $breakpoint576) {
      flex-direction: column;
      align-items: center;
    }
  }

  &__nav-item {
    margin-right: 20px;

    @media (max-width: $breakpoint576) {
      margin-right: 0;
      margin-bottom: 20px;
    }
  }

  &__nav-btn {
    &--active {
      color: $accent-color;
    }
    &:focus {
      color: $accent-color;
    }
  }

  &__content {
    padding-top: 20px;
    border-top: 1px solid rgba(20, 16, 36, 0.11);
    max-height: 378px;
  }

  &__panel {
    display: none;
    &--active {
      display: block;
    }
  }
}

.simplebar-track.simplebar-vertical {
  background: rgba(20, 16, 36, 0.08);
  border-radius: 40px;
  width: 5px;
  margin-top: 20px;
}

.simplebar-scrollbar:before {
  left: 0;
  right: 0;
  width: 5px;
  opacity: 1;
  background: #141024;
  border-radius: 40px;
}
